.page {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: $color-white;
  scroll-behavior: smooth;

  &__body {
    background-color: #1d1d1d;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    z-index: 5;

    &:target {
      transform: translateX(0);
    }
  }

  &__main {
    padding: 0 20px 120px;

    @include onTablet {
      padding: 0 72px 120px;
    }

    @include onDesktop {
      padding: 0 120px 182px;
    }
  }
}
