.about {
  &__title {
    margin: 0 0 32px;

    font-weight: 500;
    font-size: 32px;
    line-height: 100%;

    letter-spacing: -1.5px;

    color: $color-white;

    text-align: left;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }
  }

  &__text {
    display: block;

    font-weight: 500;
    font-size: 23px;
    line-height: 140%;

    color: $color-white;

    @include onTablet {
      display: grid;
    }
  }
}
